import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	data() {
		return {

		}
	},
	onLoad(e) {
		if (e.type === '1') {
			uni.showToast({
				icon: 'none',
				title: '请先选择漫画阅读偏好!'
			})
		}
	},
	computed: {
		...mapState(['userInfo']),
	},
	methods: {
		...mapMutations(['setUserSexType']),
		switchsex() {
			this.setUserSexType((this.userInfo.sex === 132) ? 2 : 1)
		}
	}
}
